.petitions_container {
    display: grid;
    grid-template-columns: 85% 15%;
    gap: 60px 0;
    .petition {
        display: flex;
        flex-direction: column;
        grid-column: 1/2;
        width: 90%;
        height: 250px;
        border: 1px solid $border_color;
        border-radius: 5px;
        overflow-y: hidden;
        .trending {
            font-size: 12.8px;
            padding: 5px 0;
            color: $text_color;
            border-bottom: 1px solid $border_color;
            span {
                font-weight: 700;
            }
        }
           a {
            text-decoration: none;
            cursor: pointer;
            .title {
                color: $title_color;
                padding: 10px 0 5px;
                transition: .2s ease-in-out;
                &:hover {
                    color: $blue;
                }
            }
           }
           .block {
            display: flex;
            padding: 5px;
           .petition_block {
            height: 200px;
            .petition_content {
                width: 70%;
                height: 40%;
                margin: 5px 0;
                overflow: hidden;
               }
               #petition_data {
                margin-top: 50px;
                .petition_author,
                .petition_date {
                    font-size: 12.8px;
                   }
               }
           }
        }
           .petition_image {
            img {
                width: 200px;
                border-radius: 5px;
            }
           }
           
           
    }
    .topics_container {
        grid-column: 2/3;
        grid-row: 1/-1;
        position: sticky;
        top: 50px;
        bottom: 0;
        .topics {
            display: flex;
            flex-direction: column;
            gap: 10px;
            span {
                text-transform: capitalize;
                border: 1px solid;
                border-color: $border_color;
                border-radius: 15px;
                cursor: pointer;
                color: $title_color;
                padding: 5px 10px;
                text-align: center;
                transition: .2s ease-in-out;
                &:hover {
                    border-color: $blue;
                    color: $blue;
                }
            }
        }
    }
    @import 'loadMoreBtn';
}